<template>
  <div class="generate-cards">
    <div class="header">
      <h2>卡密生成</h2>
    </div>

    <div class="generate-form">
      <div class="form-group">
        <label>充值金额</label>
        <div class="amount-input">
          <span class="currency">¥</span>
          <input 
            type="number" 
            v-model="form.amount"
            min="0"
            step="0.01"
            placeholder="请输入充值金额"
          >
        </div>
      </div>

      <div class="form-group">
        <label>生成数量</label>
        <div class="count-input">
          <button 
            class="count-btn" 
            @click="decreaseCount"
            :disabled="form.count <= 1"
          >
            <i class="fas fa-minus"></i>
          </button>
          <input 
            type="number" 
            v-model="form.count"
            min="1"
            max="100"
            placeholder="请输入生成数量（最多100个）"
          >
          <button 
            class="count-btn"
            @click="increaseCount"
            :disabled="form.count >= 100"
          >
            <i class="fas fa-plus"></i>
          </button>
        </div>
      </div>

      <button 
        class="generate-btn"
        @click="generateCards"
        :disabled="!isFormValid || loading"
      >
        <i class="fas fa-plus-circle"></i>
        {{ loading ? '生成中...' : '生成卡密' }}
      </button>
    </div>

    <!-- 生成结果展示 -->
    <div v-if="generatedCards.length > 0" class="result-section">
      <div class="result-header">
        <h3>生成结果</h3>
        <button class="export-btn" @click="exportToExcel">
          <i class="fas fa-file-export"></i>
          导出Excel
        </button>
      </div>

      <div class="cards-grid">
        <div v-for="card in generatedCards" :key="card.cardNo" class="card-item">
          <div class="card-header">
            <span class="card-label">卡号</span>
            <button class="copy-btn" @click="copyCardNo(card.cardNo)">
              <i class="fas fa-copy"></i>
            </button>
          </div>
          <div class="card-no">{{ card.cardNo }}</div>
          <div class="card-info">
            <span>金额: ¥{{ formatAmount(card.amount) }}</span>
            <span>状态: {{ card.status === 'unused' ? '未使用' : '已使用' }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GenerateCards',
  data() {
    return {
      form: {
        amount: '',
        count: 1
      },
      loading: false,
      generatedCards: []
    }
  },
  computed: {
    isFormValid() {
      const amount = parseFloat(this.form.amount);
      const count = parseInt(this.form.count);
      return amount > 0 && count > 0 && count <= 100;
    }
  },
  methods: {
    formatAmount(amount) {
      return parseFloat(amount).toFixed(2);
    },
    increaseCount() {
      if (this.form.count < 100) {
        this.form.count++;
      }
    },
    decreaseCount() {
      if (this.form.count > 1) {
        this.form.count--;
      }
    },
    async generateCards() {
      if (!this.isFormValid || this.loading) return;
      
      try {
        this.loading = true;
        const response = await this.$api.post('/api/recharge-cards', this.form);
        this.generatedCards = response.data.cards;
        this.$message.success('卡密生成成功');
      } catch (error) {
        this.$message.error(error.response?.data?.message || '生成卡密失败');
      } finally {
        this.loading = false;
      }
    },
    copyCardNo(cardNo) {
      navigator.clipboard.writeText(cardNo)
        .then(() => {
          this.$message.success('卡号已复制');
        })
        .catch(() => {
          this.$message.error('复制失败');
        });
    },
    exportToExcel() {
      // 创建CSV内容
      const csvContent = [
        ['卡号', '金额', '状态', '生成时间'].join(','),
        ...this.generatedCards.map(card => [
          card.cardNo,
          this.formatAmount(card.amount),
          card.status === 'unused' ? '未使用' : '已使用',
          new Date(card.createdAt).toLocaleString()
        ].join(','))
      ].join('\n');

      // 创建Blob对象
      const blob = new Blob(['\ufeff' + csvContent], { 
        type: 'text/csv;charset=utf-8;' 
      });

      // 创建下载链接
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', `卡密列表_${new Date().toLocaleDateString()}.csv`);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  },
  created() {
    // 检查权限
    const userRole = localStorage.getItem('userRole');
    if (userRole !== 'superadmin') {
      this.$router.push('/channel-market');
      this.$message.error('权限不足');
    }
  }
}
</script>

